<template>
	<div class="form-box">
		<el-form ref="form" :model="form" label-width="80px">
			<el-form-item label="vflow">
				<el-input v-model="form.flow_key" :disabled="true"></el-input>
			</el-form-item>
			<el-form-item label="cids">
				<el-input
					type="textarea"
					placeholder="cid1
cid2
cid3
cid4
cid5
cid6"
					rows="30"
					v-model="form.cids_str"
				></el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="onSubmit">提交</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
    import { addFlowInstances } from "../../api/index";
    export default {
        name: "flowInsAdd",
        data() {
            return {
                body: {
                    vflow: this.$route.params.flowKey,
                    cids: []
                },
                form: {
                    flow_key: this.$route.params.flowKey,
                    cids_str: ""
                },
            };
        },
        methods: {
            onSubmit() {
                this.body.cids = []
                if (this.form.cids_str == "") {
                    this.$message.error("输入为空");
                    return
				} else {
                    console.log("cidstr", this.form.cids_str)
                    this.body.cids = this.form.cids_str.split('\n')
				}
                addFlowInstances(this.body).then(res => {
                    if (res.code == 'A0001') {
                        this.$message.success("success:" + res.data.success + "  error:" + res.data.fail)
						this.form.cids_str = ""
                        this.body.cids = []
					} else {
                        this.$message.error("添加失败")
					}
                });
            }
        },
        created() {
        }
    };
</script>



