<template>
    <div>
        <div class="container">
            <ul class="innertab">
                <li class="innertab" v-for="(tab,index) in tabs" @click="toggle(index,tab.view, tab.type)" :class="{active:active==index}">
                    {{tab.type}}
                </li>
            </ul>
            <hr>
            <component style="padding-top: 20px" :is="currentView"></component>
        </div>
    </div>
</template>

<script>
import flowdetail from '../common/FlowDetail'
import flowdag from '../common/FlowDag'
import flowinstance from '../common/FlowInstance'
import flowInsAdd from '../common/FlowInsCreate'
export default {
    name: 'Flow',
    data() {
        return {
            active:0,
            currentType: 'detail',
            currentView:'flowdetail',
            tabs:[
                {
                    type:'details',
                    view: 'flowdetail'
                },
                {
                    type: 'dag',
                    view: 'flowdag'
                },
                {
                    type: 'ins',
                    view: 'flowinstance'
                },
                {
                    type: 'create',
                    view: 'flowInsAdd'
                }
            ],
        };
    },
    methods:{
        toggle(i,v, t){
            console.log("toggle", i, v, t)
            this.active=i;
            this.currentView=v;
            this.currentType=t;
        }
    },
    components:{
        flowdag,
        flowdetail,
		flowinstance,
        flowInsAdd,
    }
};
</script>

<style>
    ul.innertab{
        width:200px;
        display:flex;
        padding-bottom: 10px;
    }
    ul li.innertab{
        width:300px;
        height:20px;
		padding-left: 10px;
		padding-right: 10px;
        display: inline-flex;
        border-right:1px solid #ddd;
        justify-content: center;
        align-items: center;
        cursor:pointer
    }
    ul li.active{
        color: #00a854;
    }
</style>



